import { _decorator, Component, Node } from "cc";
import { QRCode, QRErrorCorrectLevel } from "./QRCode";
import * as cc from "cc";
const { ccclass, property } = _decorator;

@ccclass("test")
export class test extends Component {
  start() {
    // let qrcode = new QRCode(-1, QRErrorCorrectLevel.H);
    // console.error(qrcode);
    // qrcode.addData("www.baidu.com");
    // qrcode.make();
    // console.error(qrcode);
    let ctx = this.node.addComponent(cc.Graphics);
    this.QRCreate(ctx, "www.baidu.com");
  }

  QRCreate(ctx, url) {
    var qrcode = new QRCode(-1, QRErrorCorrectLevel.H);
    qrcode.addData(url);
    qrcode.make();

    ctx.fillColor = cc.Color.BLACK;
    //块宽高
    var tileW =
      this.node.getComponent(cc.UITransform).width / qrcode.getModuleCount();
    var tileH =
      this.node.getComponent(cc.UITransform).height / qrcode.getModuleCount();

    // draw in the Graphics
    for (var row = 0; row < qrcode.getModuleCount(); row++) {
      for (var col = 0; col < qrcode.getModuleCount(); col++) {
        if (qrcode.isDark(row, col)) {
          ctx.fillColor = cc.Color.WHITE;
          var w = Math.ceil((col + 1) * tileW) - Math.floor(col * tileW);
          var h = Math.ceil((row + 1) * tileW) - Math.floor(row * tileW);
          ctx.rect(Math.round(col * tileW), Math.round(row * tileH), w, h);
          ctx.fill();
        }
      }
    }
    ctx.stroke();
  }

  update(deltaTime: number) {}
}
